	*{
		margin: 0;
		padding: 0;
	}
@import url('https://fonts.googleapis.com/css?family=Lato:300i,400,400i,700i,900&subset=latin-ext');
// font-family: 'Lato', sans-serif;
.Wrap{
	padding-top: 25px;
	padding-bottom: 50px;
	min-width: 320px;
	font-family: Lato,Helvetica Neue,Helvetica,sans-serif;
	color: hsla(0, 0%, 0%, 0.75);
	background-color: rgba(0, 0, 0, 0.795);

}
.container{
	margin: 0 auto;
	max-width: 680px;
	padding-right: 5%;
	padding-left: 5%;
	line-height: 1.67;
	font-family: 'Lato', sans-serif;
	padding-bottom: 25px;
	padding-top: 25px;
	text-shadow: 0 1px 0 #fff;
}
.function{
	text-align: center;
	// padding-top: 25px;
	font-size: 27px;
}
.header{
	margin-top: 15px;
	margin-bottom:15px;
	&:after{
		content: "";
		clear: both;
		display: table;
		height: 0;
		visibility: hidden;
	}
}
.contact{
	float: right;
	display: flex;
	flex-direction: column;
	align-items: center;
	font-size: 16px;
	width: 65%;
	margin-top: 20px;
}
.myfoto{
	width: 35%;
	height: auto;
	float: left;
	box-shadow: 0 0 10px rgba(90, 90, 90, 0.432),
	inset 0 0 10px rgba(90, 90, 90, 0.432);
	border-radius: 4px;
}
.contact span{
	font-weight: 700;
	font-size: 15px;
	margin-right: 5px;

}
.contact__name{
	// margin-top: 5px;
	font-size: 18px;
	text-align: center;
	// margin-bottom: 5px;
}
.noWrap{
	white-space: nowrap
}
@media screen and (max-width:600px){
	.myfoto{
		width: 40%;
	}
	.contact{
		width: 55%;
		margin-top: 0px;
	}
}
@media screen and (max-width:450px){
	.header{
		float: boath;
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.contact{
		width: 100%;
		margin-top: 10px;
	}
	.myfoto{
		width: 150px;
	}

}
.main{
	position: relative;
	margin: 0 auto;
	padding-bottom: 25px;
	overflow: hidden;

}
.main__head{
	font-size: 18px;
	margin-top: 8px;
	margin-left: 20px;

}
.main__head-4{
	font-size: 16px;
}
.main__text{
	font-style: italic;
	text-align: justify;
}
.main__text li{
	margin-left: 20px;
}
.main__item{
	margin-left: 15px;
}
.date, .bold{
	font-weight: bold;
}


.paper {
	margin: 15px auto;
	background: #fffffd;
	box-shadow:inset 0 0 1px rgba(0, 0, 0, 0.5),
	3px 3px 8px rgba(0, 0, 0, 0.4);
  }
.src{
	width: 20%;
	display: block;
	position: absolute;
	right: 0px;
	bottom: 0px;
	display: none;
	&:hover .src__qr{
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
		transition: 0.5s ease-in-out;
	}
	&:hover .src__text{
		color:#007CC2;
		transition: 0.5s ease-in-out;

	}

  }
.src__qr{
	width:100%;
	height: auto;
	border-radius: 5px;
	transition: 0.5s ease-in-out;
	&:active{
		box-shadow: 0 0 20px rgba(0, 0, 0, 0.9);
		transition: 0.15s ease;

	}

}
.src__text{
	width: 100%;
	font-size: 14px;
	display: flex;
	justify-content: space-around;
	font-style: italic;
	font-weight: 700;
	position: relative;
	bottom: 12px;
	color: rgba(0, 0, 0, 0.4);
	transition: 0.3s ease-in-out;
	visibility: inherit;
}
@media screen and (max-width:550px){
	.src{
		position: static;
		margin: 0 auto;
		margin-top: 15px;
	}
	.src__text{
		font-size: 14px;
	}
}
@media print /*screen */{
	.myfoto{
		width: 165px;
	}

	.src{
		display: block;
	}
	body * {
		visibility: hidden;
	  }
	
	  .container *{
		visibility: visible;
	  }
	
	.container  {
		position: absolute;
		// width: 100%;
		// min-height: 100%;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		max-width: 700px;
		padding-bottom: 15px;
		padding-top: 15px;

	}
}